import React from 'react'
import ModelStore from "../stores/ModelStore";
import * as MyActions from "../actions/MyActions";
import { dict } from '../Dict';

export default class Default extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            file: null,
            token: null,
            id: null,
            uuid: null,
            uploadable: ' ',
            uploadNotice: 'is-hidden',
            convertNotice: 'is-hidden',
            uploadProgress: 'is-hidden',
            convertProgress: 'is-hidden',
            converted: false,
            convertedLink: ' '
        }
        this.getInstance = this.getInstance.bind(this)
        this.onChange = this.onChange.bind(this)
        //this.fileUpload = this.fileUpload.bind(this)
    }
    onFormSubmit(e) {
        e.preventDefault() // Stop form submit
        this.fileUpload(this.state.file).then((response) => {
            console.log(response.data);
        })
    }
    onChange(e) {
        this.setState({ file: e.target.files[0], uploadProgress: ' ' },
            () => MyActions.fileUpload('uploads', this.state.id, this.state.file, this.state.token));
    }


    componentWillMount() {
        ModelStore.on("got_instance", this.getInstance);
        ModelStore.on("file_posted", this.getInstance);
    }

    componentWillUnmount() {
        ModelStore.removeListener("got_instance", this.getInstance);
        ModelStore.removeListener("file_posted", this.getInstance);
    }

    componentDidMount() {


    }

    getInstance() {
        var upload = ModelStore.getIntance()
        var klass = ModelStore.getKlass()
        if (upload && klass === 'Upload') {
            this.setState({
                uuid: upload.uuid,
                uploadNotice: ' ',
                convertNotice: ' ',
                uploadProgress: ' is-hidden',
                convertProgress: ' ',
                
            });
        }
        if (upload && klass === 'UploadShow' && upload.converted) {
            this.setState({
                converted: upload.converted,
                convertNotice: ' is-hidden',
                convertProgress: ' is-hidden',
                uploadNotice: ' is-hidden',
                convertedLink: upload.converted_link,
                uploadable: ' is-hidden',
            });
        }
        console.log(upload, klass)
        setTimeout(function () {
            if (this.state.uuid && !this.state.converted) {
                MyActions.getInstance('uploads', this.state.uuid, this.state.token);
            }
        }.bind(this), 6000)
    }

    converted_link() {

        if (this.state.converted) {

            return (
                <React.Fragment>
                    <nav className="level">
                        <p className="level-item has-text-centered">
                            <a class="button is-success" href={this.state.convertedLink}>{dict.download}</a>
                        </p>
                    </nav>

                    <nav className="level">
                        <p className="level-item has-text-centered">
                            <a class="button is-warning is-light" href="/">{dict.again}</a>
                        </p>
                    </nav>
                </React.Fragment>
            )
        }
    }


    render() {

        return (
            <React.Fragment>
                <div className="container">
                    <section className="section">
                        <nav className="navbar" role="navigation" aria-label="main navigation">
                            <div className="navbar-brand">
                                <a className="navbar-item" href="/">
                                    <img src="/logo60.png" />
                                </a>
                                <a role="button" className="navbar-burger burger" aria-label="menu" aria-expanded="false" data-target="navbarBasicExample">
                                    <span aria-hidden="true"></span>
                                    <span aria-hidden="true"></span>
                                    <span aria-hidden="true"></span>
                                </a>
                            </div>

                            <div id="navbarBasicExample" className="navbar-menu">
                                <div className="navbar-start">
                                    <a className="navbar-item">{dict.home}</a>
                                </div>
                                <div className="navbar-end">
                                    <div className="navbar-item">

                                    </div>
                                </div>
                            </div>
                        </nav>
                    </section>

                    <section className="hero is-dark">
                        <div className="hero-body">
                            <div className="container">
                                <h2 className="subtitle">{dict.file_preparation_service}</h2>
                            </div>
                        </div>
                    </section>
                    <section className="section">
                        <div className="columns">
                            <div className="column">
                                <nav className={"level "+ this.state.uploadable}>
                                    <p className="level-item has-text-centered">
                                        <div className="file is-info is-boxed">
                                            <label className="file-label">
                                                <input className="file-input" type="file" name="resume" onChange={this.onChange} accept=".pdf"></input>
                                                <span className="file-cta">
                                                    <span className="file-icon">
                                                        <i className="fas fa-2x fa-file-pdf"></i>
                                                    </span>
                                                    <span className="file-label mt-4">{dict.choose_a_file}</span>
                                                </span>
                                            </label>
                                        </div>
                                    </p>
                                </nav>

                                <div class={"control " + this.state.uploadProgress}>
                                    <progress class="progress is-small is-dark" max="100">15%</progress>
                                </div>

                                <div className={"notification is-success is-light " + this.state.convertNotice}>
                                    <button className="delete"></button>
                                    {dict.your_uuid_is}:
                                    <strong> {this.state.uuid}</strong>
                                </div>

                                <div class={"control " + this.state.convertProgress}>
                                    <progress class="progress is-small is-danger" max="100">15%</progress>
                                </div>

                                <div className={"notification is-warning is-light " + this.state.uploadNotice}>
                                    <button className="delete"></button>
                                    <strong> {dict.processing_may_take_some_time}</strong>
                                </div>

                                {this.converted_link()}
                            </div>
                        </div>
                    </section>

                </div>
            </React.Fragment>
        )
    }
}
